import Vue from 'vue'
import Router from 'vue-router'
import home from './components/Home'
import documents from './components/Documents'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/documents',
      name: 'documents',
      component: documents,
      meta: { requiresAuth: true }
  }
  ]
})
